import { Component, OnInit } from "@angular/core";
import { AngularFireAuth } from "@angular/fire/auth";
import {
  AngularFirestore,
  AngularFirestoreDocument
} from "@angular/fire/firestore";
import { Seller } from "./seller";
import { SellerformService } from "./sellerform.service";
import * as firebase from "firebase";
import { AuthService } from "../auth.service";

import {
  VERSION,
  MatDialogRef,
  MatDialog,
  MatSnackBar,
  MAT_DIALOG_DATA
} from "@angular/material";
import { from } from "rxjs";
import { StateServiceService } from "../state-service.service";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";

@Component({
  selector: "app-sellerform",
  templateUrl: "./sellerform.component.html",
  styleUrls: ["./sellerform.component.css"]
})
export class SellerformComponent implements OnInit {
  isLoggedIn: Boolean = true;
  userData: any;
  Seller: Seller = new Seller();
  submitted = false;
  isLoading: boolean = false;
  return: any;
  version = VERSION;
  ConfirmDetailService: any;

  constructor(
    public authService: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth,
    private firestore: AngularFirestore,
    private SellerformService: SellerformService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private router: Router,
    private stateService: StateServiceService
  ) {}

  ngOnInit() {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem("user", JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem("user"));
        this.LoggedIn();
      } else {
        localStorage.setItem("user", null);
        JSON.parse(localStorage.getItem("user"));
        this.LoggedOut();
      }
    });
  }

  private LoggedIn() {
    this.isLoggedIn = true;
  }
  private LoggedOut() {
    this.isLoggedIn = false;
  }
  newCustomer(): void {
    this.submitted = false;
    this.Seller = new Seller();
  }

  onSubmit() {
    // Get Seller variable into local scope for html
    this.stateService.Seller = this.Seller;
    this.router.navigate(["confirmation"]);
  }
}
