import { Component, OnInit } from "@angular/core";
import { SellermatchesService } from "../sellermatches/sellermatches.service";
import { StateServiceService } from "../state-service.service";

@Component({
  selector: "app-sellermatches",
  templateUrl: "./sellermatches.component.html",
  styleUrls: ["./sellermatches.component.css"]
})
export class SellermatchesComponent implements OnInit {
  return: any;
  userData: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  user: any;
  uid: any;
  propertyDetails = [];
  matchedProperties = [];

  constructor(
    private SellermatchesService: SellermatchesService,
    private stateService: StateServiceService
  ) {}

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;

    // Fetch details
    this.SellermatchesService.getSellerProperties(this.uid).subscribe(res => {
      res.forEach(item => {
        if (item.data().postcode == this.stateService.Seller.postcode) {
          this.matchedProperties.push(item.data()); //Use this object to populate html
        }
      });
      console.log(this.matchedProperties);
    });
  }
}
